









export default {
	data() {
		return {
			title:'',
			strings:''
		};
	},
	onLoad:function(e){
		console.log(e);				
		uni.request({
			//url: 'https://news-at.zhihu.com/api/4/news/latest/9702106',
			url: 'https://news-at.zhihu.com/api/4/news/'+e.id,
			method: 'GET',
			data: {},
			success: res => {
				//console.log(res.data);
				this.title = res.data.title;
				this.strings = res.data.body;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}



